.container {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.headerRow {
  width: 60vw;
  min-width: 300px;
  margin: auto auto 10px auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.headerText {
  font-weight: bold;
  font-size: 52px;
}

.headerLogo {
  height: 100px;
  width: 100px;
}

.nextButton {
  position: absolute;
  bottom: 3vh;
  right: 1.5vw;
  font-size: 2vw;
  line-height: 3vw;
  opacity: 0.7;
  color: #fff;
  user-select: none;
  outline: none;
  text-decoration: none;
  cursor: pointer;
}
.nextButton:hover {
  opacity: 1;
}

.arrow {
  margin: auto 1.5vw;
  font-size: 1.6vw;
}

.standingsHolder {
  width: 60vw;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

.standingContainer {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 2vh 0;
}

.placeText {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 6px;
  font-size: 34px;
  height: 50px;
  width: 50px;
  margin-right: 15px;
  font-weight: bold;
  color: #141e32;
  background-color: #fff;
}

.nameText {
  width: calc(60vw - 125px);
  height: 50px;
  line-height: 50px;
}

.scoreText {
  width: 60px;
  font-weight: bold;
  font-size: 36px;
  line-height: 50px;
  text-align: right;
}

.divider {
  width: 100%;
  height: 3px;
  background-color: rgba(255, 255, 255, 0.7);
}

.tooltip {
  font-size: 15px;
  padding: 6px 13px;
}
